.login{
  display: flex;
  height: 100vh;
  min-height: 500px;
  .desc{
    background: url('../../assets/ku.jpg') 100% no-repeat;
    background-size: cover;
    padding: 30px;
    flex: 2;
    height: 100%;
    background-color: #2B579A;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fafafa;
    position: relative;
    @media (max-width: 992px) {
      display:none !important;
    }
    .overlay{
      background-color: #000000a8;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 100;
    }
    img{
      margin-bottom: 20px;
    }
    span{
      text-shadow: 0px 0px 2px #0000009f;
      font-weight: bold;
      color: inherit;
      font-size: 26px;
    }
    p{
      font-size: 15px;
      text-align: center;
      color: inherit;
      margin-top: 30px;
      width: 500px;
    }
  }
  .loginForm{
    box-shadow: inset 0 0 5px #0000009f;
    flex:1;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    .highlightedLink{
      cursor: pointer;
      font-weight: bold;
  }
    .medium{
      @media (min-width: 993px) {
        display:none !important;
      }
    }
    .large{
      @media (max-width: 992px) {
        display:none !important;
      }
    }
    h2{
      color: #2B579A;
      text-align: center;
      margin: 25px 5px;
    }
    .login-form {
      max-width: 400px;
      min-width: 350px;
    }
    .login-form-forgot {
      float: right;
    }
    .login-form-button {
      width: 100%;
    }
  }
}