.container{
  padding:15px;
  min-height: calc(100vh - 100px);
  max-width: 1100px;
  min-width: 400px;
  margin: 15px auto;
  overflow: auto;
  background: #fafafa;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
  border-radius: 4px;
  .scrollBtn{
    position: absolute;
    bottom: 25px;
    right: 25px;
    opacity: 0.5;
    border-color:#d9d9d9 !important;
      color: #000000a6 !important;
    &:hover{
      opacity: 1;
    }
    &:active{
      box-shadow: none;
    }
  }
}