.container{
    margin: 24px 16px;
    height: calc(100vh - 120px);
    min-height: 280px;
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
    border-radius: 5px;
    background-color: #fff;
    .departmentItem{
      padding: 10px 18px;
      margin: 10px;
      background: #f1f1f1;
      width: fit-content;
      font-size: 14px;
      font-weight: bold;
      line-height: 35px;
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      span{
        text-transform: uppercase;
        margin-right: 15px;
      }
    }
  }