.post{
    width: 100%;
    padding-bottom: 15px;
    margin: 15px auto;
    border-bottom: 1px solid #00000014;
    .link{
        padding: 4px;
        position: relative;
        left: 635px;
        top: 24px;
        .dropDown{
            :hover{
                margin: 3px;
                border: 2px solid #00000014;
                width: 80px;
                display: hidden;
                .dropDownBtn{
                    cursor: pointer;
                    text-align: center;
                    padding: 3px;
                }
            }
        }
    }
    .mainInfo{
        display: flex;
        align-items: center;
        justify-content: space-between;
        >div{
            display: flex;
            align-items: center;
        }
        >span{
            cursor: pointer;
        }
        h2{
            font-size: 14px;
            margin: 0;
        }
        .timeStamp{
            line-height:1;
            cursor: pointer;
            time{
                font-size: 11px;
            }
        }
    }
    .postData{
        padding: 10px 20px 8px 40px;  
        font-size: 14px;
    }
    .reactionContainer{
        margin-left: 40px;
       display: flex;
       align-items: center;
       div{
           font-size: 12px;
           font-weight: bold;
           margin-right: 15px;
           display: flex;
           align-items: center;
       }
    }
}