.alumniProfile{
  margin: 24px 16px;
  min-height: 280;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
  border-radius: 5px;
  background-color: #fff;
  height: calc(100% - 50px);
  .top{
    padding: 15px;
    display: flex;
    justify-content: space-between;
    height: 210px;
    overflow-y: auto;
    position: relative;
    .backBtn{
      position: absolute;
      top: 15px;
      right: 15px;
    }
  }
  .profileBanner{
    display:flex;
    padding: 15px;
    width:600px;
    .profileImg{
      width: 100px;
      height: 100px;
    }
    .profileTopData{
      margin-left: 15px;
      h2{
        margin:0;
        font-weight: bold;
      }
      span{
        font-size: 12px;
        color:#999;
      }
    }
  }
  .personalDetails{
    height: calc(100% - 220px);
    margin-top: 10px;
      h2{
        font-size: 18px;
      }
      h3{
        background: #00000014;
        padding: 6px 12px;
      }
    .info{
      height: calc(100% - 50px);
      overflow-y: auto;
      display: flex;
      flex-wrap: wrap;
    }
    .infoItems{
      margin: 15px;
      min-width: 450px;
      width: 40%;
      @media (max-width:1200px) {
        width: 80%;
        margin: 15px auto;
      }
      @media (max-width:768px) {
        width: 96%;
        margin: 15px auto;
      }
      div{
        display: flex;
        width: 100%;
        padding: 4px 12px;
      }
    }
  }
}