.container{
  margin: 24px 16px;
  min-height: 280;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
  border-radius: 5px;
  background-color: #fff;
  height: calc(100% - 50px);
  .pendingAlumni{
    padding: 12px 25px;
    margin: 10px;
    background: #f1f1f1;
    width: fit-content;
    font-size: 14px;
    font-weight: bold;
    border-radius: 4px;
    cursor: pointer;
    span{
      font-size: 11px;
      color: #888;
    }
  }
}