.register{
  display: flex;
  height: 100vh;
  width: 100%;
  .large{
    @media (max-width: 992px) {
      display:none !important;
    }
  }
  .medium{
    @media (min-width: 993px) {
      display:none !important;
    }
  }
  .sideBar{
    flex: 1.5;
    background-color: #2B579A;
    color: #fafafa;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 40px 15px;
    box-sizing: border-box;
    span{
      text-shadow: 0px 0px 2px #0000009f;
      font-weight: bold;
      color: inherit;
      margin: 20px 0;
      text-align: center;
      font-size: 22px;
    }
    p{
      font-size: 15px;
      text-align: justify;
      color: inherit;
      margin-top: 30px;
      width: 90%;
    }
  }
  .main{
    min-height: 450px;
    overflow-y: auto;
    padding: 30px 0;
    @media (min-width: 993px) {
      margin: auto;
    }
    flex: 4;
    text-align: center;
    .highlightedLink{
        cursor: pointer;
        font-weight: bold;
    }
    .h-font{
      margin: 20px;
    }
    .stepsRegister{
      text-align: left;
      width: 90%;
      margin: auto;
    }
    .steps-action{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .ant-form-item{
      margin-bottom: 8px;
    }
    .form{
      margin: 30px 10px;
    }
  }
}