body {
  margin: 0;
  padding: 0;
  font-family: 'Lato',-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
}

/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
img{
  -webkit-user-drag: none;
}
.h-font{
  font-family: 'Raleway', sans-serif;
}
.s-font{
  font-family: 'Lato', sans-serif;
}

.fullscreenLoader{
  height: 100vh;
  width: 100%;
  background-color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.ant-table-body{
  overflow-x: auto !important;
}

.ant-drawer-content-wrapper{
  max-width: 100% !important;
  min-width: 400px !important;
}

.dot-loader{
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-color: #2B579A;
  position: relative;
  -webkit-animation: 1.2s scaleDown ease-in-out infinite ;
  animation: 1.2s scaleDown ease-in-out infinite ;
}

.dot-loader:nth-child(2){
  margin: 0 6px;
  -webkit-animation: 1.2s scaleDown ease-in-out infinite .15555s;
  animation: 1.2s scaleDown ease-in-out infinite .15555s;
}

.dot-loader:nth-child(3){
  -webkit-animation: 1.2s scaleDown ease-in-out infinite .300000s;
  animation: 1.2s scaleDown ease-in-out infinite .300000s;
}

@-webkit-keyframes scaleDown{
  0%, 80%, 100%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scaleDown{
  0%, 80%, 100%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



.commentItem i{
  display: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.commentItem:hover i{
  display: block
}

@media print
{    
    .no-print, .no-print *
    {
        display: none !important;
    }
}
